import { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { Navigate } from "react-router-dom";

function ToonCard(props){
    let {toon} = props;
    let navigate = useNavigate();

    return(
      <div className="col-sm-6 col-md-3 box" onClick={()=>{
        navigate(`/toon_details/${toon.id}`);
      }}>
        <img className='bookImg' height={240} width={173} src={toon.src}></img>
        <p className='title'>{toon.title}</p>
        <div className='description'>
          <span className='author'> {toon.author}</span>
          <span className='star'> {toon.star}</span>
        </div>

         {/* props  toon  id  /toon_details/${toon.id}  .(  router id  toon  props  ToonDetails   )*/}
        <span className="linkTo"><Link style={{ textDecoration: "none" }} to={`/toon_details/${toon.id}`}> </Link></span>
      
      </div>
    )
  }

  export default ToonCard;