import { configureStore, createSlice } from "@reduxjs/toolkit";

let user = createSlice({
    name : 'userName',
    initialState : {name: 'User1234', age : 24, grade:'VIP ', phone:'010-1111-1111', email: 'leesuhyun05505@gmail.com'},
    //  
    reducers : {
        //  //state initialState  
        changeName(state, action){
            //this.name .
            state.name = action.payload;
        },
        increaseNum(state, a){
            state.age = state.age + a.payload; 
            // state.age = state.age + 1
        }
    }
})

export default user;