import { useEffect, useState } from 'react';
import Nav from 'react-bootstrap/Nav';

function Tab(props) {

    //   toon  
    let { data } = props
    console.log(':', data);
    //tap 0~2      state 
    let [tab, setTab] = useState(0);
    let [color1, setColor1] = useState('blue');
    let [color2, setColor2] = useState('black');
    let [color3, setColor3] = useState('black');

  return (
    <>
        <Nav variant="tabs" defaultActiveKey="#">
            <Nav.Item onClick={()=>{
                setTab(0);
                setColor1('blue');
                setColor2('black');
                setColor3('black');
            }}>
                <Nav.Link href="#" style={{color:color1}}></Nav.Link>
            </Nav.Item>
            <Nav.Item onClick={()=>{
                setTab(2);
                setColor1('black');
                setColor2('black');
                setColor3('blue');
            }}>
                <Nav.Link eventKey="#" style={{color:color3}}></Nav.Link>
            </Nav.Item>
            <Nav.Item onClick={()=>{
                setTab(1);
                setColor1('black');
                setColor2('blue');
                setColor3('black');
            }}>
                <Nav.Link eventKey="#" style={{color:color2}}> </Nav.Link>
            </Nav.Item>
        </Nav>

        {/* toondetail   toon    */}
        <TabContent tab={tab} data={data} ></TabContent>

    </>
  );
}

function TabContent({tab, data}){
    let [fade, setFade] = useState('');

    useEffect(()=>{
        //end 
        setFade('');
        setFade('end');
    }, [tab])

    //["", "", ... ][1] :       
    //tab        
    return (
        <div className={fade}>
            {[<div>{
                    data.reviews.map((review, index)=>{
                        return <div key={index}>{index + 1}. {review}</div>
                    })
                }</div>,
            <div> </div>,
            <div>{data.title}</div>][tab]}
        </div>
    );
}

export default Tab;