import React from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useEffect, useState, useContext } from "react";
import Tab from "../tap";
//context   
import { addItem } from "../store";
import { useDispatch } from "react-redux";

function ToonDetails(props){

  //Context   state     .
  // useContext(Context1);
  // state provider     .
  // const { test } = useContext(Context1);

  let navigate = useNavigate();
  let [count, setCount] = useState(0);
  let dispatch = useDispatch();
  

  let { toon } = props
  console.log(toon);

  
  //alert    state
  let [alert, setAlert] = useState(true);
  let [num, setNum] = useState('');
  


    useEffect(()=>{

      //5  true -> false  
      let timer = setTimeout(()=>{
        setAlert(false)
      }, 5000);
      console.log(2);

      //   
      if(isNaN(num)===true){
        window.alert('   .');
      }

      return(
        ()=>{
          //  
          clearTimeout(timer);
          console.log(1);
        }
      )
    }, [num])


    return(
      <div className="container text-center">

        {/* <h1>{test}</h1> */}
        {/* 2     */}
        { alert == true ?
          <div className="show">
            5    10% 
          </div> :
          null
        }
        
        {/* <input onChange={
          (e) => {
            // if(isNaN(e.target.value)===true){
            //   window.alert('   .');
            // }

            setNum(e.target.value);
          }
        } className="userInput" type="text" placeholder=""></input> */}


        <div className="row detailRow">
          <div className="col">
            <img className='bookImg' height={320} width={220} src={toon.src}></img>
            <div className="detailBtns">
              <button className="btn btn-primary" onClick={()=>{
                navigate("/cart");
                //    procut   
                dispatch(addItem({id: toon.id, name: toon.title, count : 1 }))
              }}></button>
              <button className="btnHeart" onClick={
                () => {
                  setCount(count + 1);
                }
              }>  </button><span className="likes">{count}</span>
            </div>
          </div>
          <div className="col colnum2">
            <div className="description2">
              <p className='detailTitle'>{toon.title}</p>
              <div>
                <span className='author'><span className="details"></span> {toon.author} </span>
                <span className='star'><span className="details"> </span>{toon.star}</span><br></br>
              </div>
              <div>
                <span> <span className="details"> </span> {toon.date} </span>
                <span> <span className="details"></span> {toon.age}</span><br></br>
              </div>
            </div>
            <p className="description3">{toon.description}</p>
          </div>
        </div>
        {/*    toon  .      */}
        <Tab data={toon}></Tab>
      </div>
    )
  }

  export default ToonDetails;