import { configureStore, createSlice } from "@reduxjs/toolkit";
import ToonDetails from "./pages/ToonDetails";
//user  slice   import.
import user from "./store/userSlice";

//redux state = slice   . usestate = createSlice
//let temp = useState('test') === createSlice({name: 'temp', initialState : 'test'})


//user.actions  user    .
//  
export let {changeName, increaseNum} = user.actions;


let stock = createSlice({
    name : 'stockName',
    initialState : [1,2,3]
})
 
//cart.js   slice test code
let product = createSlice({
    name: 'product',
    initialState: [
        { id: 0, name: '', count: 1 },
        { id: 1, name: '2', count: 1}
    ],
    reducers: {
        incraseCount(state, action){
            //  action .payload.index 
            console.log(action.payload.index);
             //  action .payload.IncreaseNum 
            console.log(action.payload.IncreaseNum);

            //  index   
            let index = action.payload.index;
            //product    index   index ,     count .
            // state[index].count += action.payload.IncreaseNum; //    .
            state[index].count = state[index].count + action.payload.IncreaseNum
        },
        //action   
        addItem(state, action){
            state.push( action.payload);
        },
        deletItem(state, action){
            //   deleteItem   /   index splice  
            state.splice(action.payload, 1);
        }
    }
});

export let {incraseCount, addItem, deletItem} = product.actions;



//      //export  index.js 
export default configureStore({
    //       .    .
    reducer : {
        // C#   user    configureStore    
        // user slice   configureStore  .
        
        user : user.reducer, //kim
        stock : stock.reducer, //money
        product : product.reducer,
    }
})
